#main-nav-toggle {
  position: fixed;
  right: 20px;
  top: 22px;
  z-index: 19901;
  text-transform: uppercase;
  font-weight: bold;
  letter-spacing: 0.075em;
  cursor: pointer;
  display: block;
  background: none;
}

#main-nav-toggle .open,
#main-nav-toggle .close {
  -webkit-transition: 0.25s;
  transition: 0.25s;
  position: absolute;
  right: 0;
  top: 0;
  color: var(--white);
}

#main-nav-toggle .open {
  border: 2px solid var(--red);
  color: var(--red);
  letter-spacing: 0.16em;
  padding: 9px 9px 8px;
  font-size: 1.1em;
}

#main-nav-toggle .open:hover {
  background: var(--red);
  color: var(--white);
}

.scroll #main-nav-toggle .open {
  background: var(--red);
  color: var(--white);
  border-color: var(--white);
}

