  background: var(--dark-red);
}

#main-nav-toggle:focus .open {
  background: var(--red);
  color: var(--white);
}

#main-nav-toggle .close {
  visibility: hidden;
  opacity: 0;
  width: 80px;
  text-align: right;
  font-weight: normal;
  top: 4px;
  font-size: 11px;
}

#main-nav-toggle .close svg {
  fill: currentColor;
  width: 24px;
  height: 24px;
  vertical-align: bottom;
  margin-right: 5px;
}

.show #main-nav-toggle {
  color: var(--white);
  background: transparent;
}

.show #main-nav-toggle .close {
  visibility: visible;
  opacity: 1;
}
.show #main-nav-toggle .close:hover {
  color: #989898;
}

.show #main-nav-toggle .open {
  visibility: hidden;
  opacity: 0;
}

